@use '../../partials/mixins' as *;

.large-menu__tabs {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
}

.large-menu__tab {
    flex: 1 0 33.33%;
    padding: 1rem;
    transition: 0.5s;
    position: relative;
    text-align: center;
    cursor: pointer;

    &--active {
        transition: 0.5s;
    }
}